import React, { useEffect, useRef } from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import styled from 'styled-components';
import { srConfig } from '@config';
import sr from '@utils/sr';
import { usePrefersReducedMotion } from '@hooks';

const StyledAboutSection = styled.section`
  max-width: 900px;
  width: 100%; /*     */
  margin: 0 auto;

  /*         */
  @media (max-width: 900px) {
    padding: 0 25px;
    box-sizing: border-box;
  }

  .inner {
    display: grid;
    grid-template-columns: 3fr 2fr;
    grid-gap: 50px;

    @media (max-width: 768px) {
      display: block;
    }
  }
`;

const StyledText = styled.div`
  /*   p  () */
  div > p {
    word-break: break-word;
  }

  /*    p   :     */
  & > p:not(:first-child) {
    font-weight: 600; /*   */
    /*       */
    /* color: var(--lightest-slate); */
    margin-top: 15px; /*      */
    margin-bottom: 5px; /*      */
  }

  ul.skills-list {
    display: grid;
    /* 769px : 3 */
    grid-template-columns: repeat(3, minmax(120px, 1fr));
    grid-gap: 0 10px;
    padding: 0;
    margin: 20px 0 0 0;
    list-style: none;

    /* 768px : 2      */
    @media (max-width: 768px) {
      grid-template-columns: repeat(2, minmax(120px, 1fr));
    }

    li {
      position: relative;
      margin-bottom: 10px;
      padding-left: 20px;
      font-family: var(--font-mono);
      font-size: var(--fz-xs);

      &:before {
        content: '';
        position: absolute;
        left: 0;
        color: var(--green);
        font-size: var(--fz-sm);
        line-height: 12px;
      }
    }
  }
`;

const StyledPic = styled.div`
  position: relative;
  max-width: 300px;

  @media (max-width: 768px) {
    margin: 50px auto 0;
    width: 70%;
  }

  .wrapper {
    /* ... (mixins.boxShadow   ) ... */
    display: block;
    position: relative;
    width: 100%;
    border-radius: var(--border-radius);
    background-color: var(--green);

    &:hover,
    &:focus {
      outline: 0;
      transform: translate(-4px, -4px);

      &:after {
        transform: translate(8px, 8px);
      }

      .img {
        filter: none;
        mix-blend-mode: normal;
      }
    }

    .img {
      position: relative;
      border-radius: var(--border-radius);
      mix-blend-mode: multiply;
      filter: grayscale(100%) contrast(1);
      transition: var(--transition);
    }

    &:before,
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: var(--border-radius);
      transition: var(--transition);
    }

    &:before {
      top: 0;
      left: 0;
      background-color: var(--navy);
      mix-blend-mode: screen;
    }

    &:after {
      border: 2px solid var(--green);
      top: 14px;
      left: 14px;
      z-index: -1;
    }
  }
`;

const About = () => {
  const revealContainer = useRef(null);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    // sr.reveal    (gatsby-starter-default )
    sr.reveal(revealContainer.current, srConfig());
  }, []);

  return (
    <StyledAboutSection id="about" ref={revealContainer}>
      <h2 className="numbered-heading">About Me</h2>

      <div className="inner">
        <StyledText>
          <div>
            <p>
                      ,
              .
            </p>
            <p>
                       , 
                  .
            </p>
            <p>
                 API   ,     
              .
            </p>
            <p>
                    ,   
               
            </p>
          </div>
          <p></p>
          <p>Backend</p>
          <ul className="skills-list">
            <li>Java</li>
            <li>Python</li>
          </ul>
          <p></p>
          <p>Frontend</p>
          <ul className="skills-list">
            <li>Html/Css</li>
            <li>Jsp</li>
            <li>JavaScript</li>
            <li>jQuery</li>
            <li>TypeScript</li>
            <li>React</li>
          </ul>
          <p></p>
          <p>Framworks / Libraries</p>
          <ul className="skills-list">
            <li>Spring Framework</li>
            <li>Spring Boot</li>
            <li>eGovFramework</li>
            <li>Django</li>
            <li>FastAPI</li>
            <li>Mybatis</li>
            <li>JPA</li>
            <li>Spring Security</li>
          </ul>
          <p></p>
          <p>Server</p>
          <ul className="skills-list">
            <li>CentOS</li>
            <li>LockyLinux</li>
            <li>Ubuntu</li>
            <li>Apache</li>
            <li>Tomcat</li>
            <li>NGINX</li>
            <li>Docker</li>
          </ul>
          <p></p>
          <p>Database</p>
          <ul className="skills-list">
            <li>MySQL/MariaDB</li>
            <li>OracleDB</li>
            <li>Tibero</li>
            <li>PostegreSQL</li>
          </ul>
        </StyledText>

        <StyledPic>
          <div className="wrapper">
            <StaticImage
              className="img"
              src="../../images/me.jpg"
              width={500}
              quality={95}
              formats={['AUTO', 'WEBP', 'AVIF']}
              alt="Headshot"
              //      
              placeholder="blurred"
              loading="eager"
            />
          </div>
        </StyledPic>
      </div>
    </StyledAboutSection>
  );
};

export default About;
