import React from 'react';
import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import { useLocation } from '@reach/router';
import { useStaticQuery, graphql } from 'gatsby';

const Head = ({ title, description, image }) => {
  const { pathname } = useLocation();

  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          pathPrefix
          siteMetadata {
            defaultTitle: title
            defaultDescription: description
            siteUrl
            defaultImage: image
            twitterUsername
          }
        }
      }
    `,
  );

  const {
    pathPrefix,
    siteMetadata: { defaultTitle, defaultDescription, siteUrl, defaultImage, twitterUsername },
  } = site;

  // SEO    URL/  
  const seo = {
    title: title || defaultTitle,
    description: description || defaultDescription,

    // OG  URL: pathPrefix     . ()
    image: `${siteUrl}${pathPrefix}${image || defaultImage}`,

    //  og:url  : pathPrefix   siteUrl  
    // URL  Gatsby  , siteUrl      
    url: siteUrl,
  };

  //   URL /   . (siteUrl    )
  if (seo.url.endsWith('/')) {
    seo.url = seo.url.slice(0, -1);
  }

  //   : pathPrefix  pathname .
  if (pathname && pathname !== '/') {
    // pathname    seo.url  .
    const cleanPathname = pathname.startsWith('/') ? pathname : `/${pathname}`;
    seo.url = `${seo.url}${cleanPathname}`;
  }

  return (
    <Helmet title={title} defaultTitle={seo.title} titleTemplate={`%s | ${defaultTitle}`}>
      <html lang="en" />
      <meta name="description" content={seo.description} />
      <meta name="image" content={seo.image} />

      {/* OG (Open Graph)  */}
      <meta property="og:title" content={seo.title} />
      <meta property="og:description" content={seo.description} />
      <meta property="og:image" content={seo.image} />
      <meta property="og:url" content={seo.url} />
      <meta property="og:type" content="website" />

      {/* Twitter   */}
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:creator" content={twitterUsername} />
      <meta name="twitter:title" content={seo.title} />
      <meta name="twitter:description" content={seo.description} />
      <meta name="twitter:image" content={seo.image} />

      <meta name="google-site-verification" content="DCl7VAf9tcz6eD9gb67NfkNnJ1PKRNcg8qQiwpbx9Lk" />
    </Helmet>
  );
};

export default Head;

Head.propTypes = {
  title: PropTypes.string,
  description: PropTypes.string,
  image: PropTypes.string,
};

Head.defaultProps = {
  title: null,
  description: null,
  image: null,
};
